table {
    border-color: #ffffff;
}

.NormalButton {
    color: black;
    display: flex;
    font-family: "Noto Sans CJK KR";
    font-size: 20px;
    font-weight: bold;
    margin-left: 20px;
    margin-right: 20px;
    border: 4px solid;
    border-color: #636363;
    border-radius: 30px;
    padding-left: 10px;
    padding-right: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 40px;
    cursor: pointer;
    transition: all 0.5s;
}

.SelectedButton {
    color: #ffffff;
    display: flex;
    font-family: "Noto Sans CJK KR";
    font-size: 20px;
    font-weight: bold;
    margin-left: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 20px;
    border: 4px solid #636363;
    background-color: #636363;
    border-radius: 30px;
    width: 200px;
    justify-content: center;
    align-items: center;
    height: 40px;
    cursor: pointer;
    transition: all 0.5s;
}

.SelectLogTypeBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}

.TableWrap {
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-top: 50px;
}
.RowTilte {
    font-family: "Noto Sans CJK KR";
    font-size: 15px;
}

.InputWrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}
.pagination {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}
ul {
    list-style: none;
    padding: 0;
}
ul.pagination li {
    display: inline-block;
    width: 30px;
    height: 30px;
    border: 1px solid #e2e2e2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
}
ul.pagination li:first-child {
    border-radius: 5px 0 0 5px;
}
ul.pagination li:last-child {
    border-radius: 0 5px 5px 0;
}
ul.pagination li a {
    text-decoration: none;
    color: #337ab7;
    font-size: 1rem;
}
ul.pagination li.active a {
    color: white;
}
ul.pagination li.active {
    background-color: #337ab7;
}
ul.pagination li a:hover,
ul.pagination li a.active {
    color: blue;
}
.page-selection {
    width: 48px;
    height: 30px;
    color: #337ab7;
}
