.MenuList {
    display: flex;
    justify-content: center;
    margin-top: 150px;
}

.MenuListItem img {
    width: 200px;
    height: 200px;
}
.MenuListItemText {
    font-family: "Noto Sans CJK KR";
    font-size: 20px;
    text-align: center;
    margin-top: 20px;
    font-weight: bold;
}

.MenuListItem {
    margin-left: 50px;
    margin-right: 50px;
    cursor: pointer;
}
